        id: 5,
        title: 'BEHIND BLOCK 42',
        cover: loreCoverChapterFive,
        text: <>

        <p>In the basement of Polkadot's Torrey Pines Estate was a clean, dark wine cellar replete with elegant and extravagant bottles of wine that would fetch a small fortune at an auction. Behind a rack of bottles in the furthest corner was a secret stairwell that descended even lower into the ground. This entrance was electronically sealed, and the maglocks would temporarily release if someone were to remove the bottle of 2004 Penfolds Block 42 Cabernet Sauvignon.</p>

        <p>Polkadot rarely brought people down to these lower levels where she kept her bleeding-edge computer tech. In fact, she could count the number of people who knew about this room on one hand.</p>

        <p>They stood in the glow of a dozen computer screens and stared in awe at Ethereum's glyph as it unpacked itself and released a cloud of holographic code floating into the air. Solana had experienced this before, but the strange yet fascinating piece of technology was no less breathtaking to admire.</p>

        <p>Polkadot, who had always been skeptical of Ethereum's wild inventions, saw the culmination of his life's work and was surprised to find herself impressed.</p>

        <p>"I've never seen anything like it," Polkadot said.</p>

        <p>"Can you understand it?" Solana asked. "Do you know what it does? Or what it's for?"</p>

        <p>Polkadot didn't respond immediately, but she thought she could decode it. After all, she'd worked with Ethereum in the past and had entertained his crackpot ideas on the interoperability of blockchains. She often tried to ignore him when he rambled about this or that for too long, but he was insistent. He was well known for repeating himself until he had been acknowledged.</p>

        <p>Despite her best efforts, some of Ethereum's theories wormed their way into her long-term memory, and as she closed her eyes, she thought she could recall just enough information to decode what was hidden inside the glyph.</p>

        <p>"This is all Federally related," Polkadot said, twirling her finger at a specific region of code that drifted past Solana's head.</p>

        <p>"Federal?" Solana asked. She stepped back and searched through the code but couldn't recognize what Polkadot was seeing. "You mean, like, the government?"</p>

        <p>"Not exactly," Polkadot said. She tilted her head and unfocused her eyes, trying to blur her vision and see deeper into the code. "It's not a government system; it's just designed to interact with the government system based on my understanding."</p>

        <p>Polkadot crossed her arms and stepped back as she huffed in frustration.</p>

        <p>"What?" Solana asked, "what's wrong?"</p>

        <p>"I can't make heads or tails of what this is trying to say," Polkadot replied. "It's not complete nonsense; it's got structure. The coding is tight, but many parts are incomprehensible despite my best efforts to read it. You and I might be good at what we do, but we have no hope of understanding what this glyph means."</p>

        <p>"You're saying Ethereum is insane," Solana asserted.</p>

        <p>"Eh, as the old saying goes," Polkadot replied. "The only difference between insanity and genius is success." She took another look at the code and nodded in slight approval. "Ethereum knew what he was doing. This code has a purpose." Polkadot threw up her hands, let them fall to her sides, and turned her eyes to Solana. "But we may never know if he's insane or a genius."</p>

        <p>Solana shook her head. "No, I'm not buying that. Ethereum got scooped up by the government for this, which means he was a threat. The government feared what he was working on. Whatever this is..." she said, twirling her finger at the holograms, " it's the real deal."</p>

        <p>"Well," Polkadot said, "unless you know another genius programmer who moonlights as a rambling madman, I don't know how you're ever going to crack this thing."</p>

        <p>"I might not need a rambling madman to crack this," Solana said. "It's Federal code, isn't it?"</p>

        <p>Polkadot was skeptical of the direction this conversation was heading. She was hesitant to agree outright. "Sure"</p>

        <p>Solana lowered her voice to a whisper, not that anyone else could overhear them in Polkadot's basement bunker. Still, her instinct was to lower her voice when invoking the authorities: "Then I'll convince a Fed to tell me what it is."</p>

        <p>Solana and Polkadot stood in silence, face to face, before Polkadot's mouth turned into a grin. She revealed her pearly white teeth, then began to laugh. After a moment of laughing  then realizing that Solana was unamused  she settled herself down and again became quiet.</p>

        <p>"Oh..." Polkadot said, "You're serious."</p>

        <p>"Oh, I'm serious," Solana said.</p>

        <p>"Well" Polkadot raised her eyebrows and shook her head. "Then you're an idiot. If you show this to the Feds, they will wipe the glyph with an electromagnet and throw you in a dark, forgotten interrogation cell. That's the last anyone will ever see of you."</p>

        <p>"Avalanche knows people in the government," Solana said.</p>

        <p>"Oh, does he?" Polkadot asked skeptically. "And where did you hear that?"</p>

        <p>"Well," Solana paused. She knew the answer sounded dumb but continued anyway. "From Avalanche."</p>

        <p>"Solana, use your brain," Polkadot said. Her tone was of an older sister trying to talk some sense into a younger one. "Avalanche says all kinds of silly things."</p>

        <p>"I know," Solana said. "I know what Avalanche is like." She steeled herself and pointed forcefully at the orbiting holographic symbols. "But, as you said, the only person who can figure out what this means is a rambling madman or a Fed. The only rambling madman I know has already been arrested, so a Fed is the only option I have left."</p>

        <p>Polkadot sighed, pointed her eyes toward the floor, and shook her head in dismay. "I guess... If that's what you choose to do with your time." She looked up and pressed her finger against Solana's chest. "But whatever you do, from here on out, keep my name out of it."</p>

        </>,

