        id: 12,
        title: 'THE FINAL AWAKENING',
        cover: loreCoverChapterTwelve,
        text: <>

        <p>"Hang on, Solana. We're almost there."</p>

        <p>Ethereum stared down softly at Solana as he cradled her in his arms. Despite her assurances that she was okay, he could see the pained expression on her face. Solana moaned as he applied pressure to the wound to stop the bleeding.</p>

        <p>"Maybe we should take her to a hospital?" the cab driver asked. He peeped back at Ethereum and Solana's pale faces from his rearview mirror. His dark eyes were full of worry.</p>

        <p>"This is not an option. The closest hospital is across the city. She'll never make it. Now, please mind your business, drive to my destination, and I'll tip you well," Ethereum snapped.</p>

        <p>"Shh," he whispered to Solana. "We're very close now."</p>

        <p>The cab pulled to a stop in a secluded alley. Tall brick walls and steel doors greeted them, a cold industrial landscape that Ethereum was all too familiar with.</p>

        <p>Shuffling Solana into his arms, he exited the cab and made his way down the alley. He waited for the cab to disappear, and when he was sure it was out of sight, he made his way to his hideout.</p>

        <p>The last steel door on the left looked like all the others, but behind it was an extensive laboratory. Ethereum had set the door with intricate digital locks, and he hurried to open them. Once inside, he carefully laid Solana down on a cot.</p>

        <p>Solana glanced around the room, wincing with each breath. "This place is incredible," she said, searching for anything to distract her from the pain. A series of high-tech computers stared back at her. But what drew her eye was a robot next to a chair. It had a series of tubes and cords that hooked into a nearby computer.</p>

        <p>"What's that for?" she asked, nodding in the robot's direction.</p>

        <p>Ethereum sank to his knees next to her, trembling fingers sliding her shirt over her wound. She gasped as the heat seared through her abdomen, followed by a sharp jolt of pain.</p>

        <p>"This is bad, Solana," he muttered, reaching for a pack below the cot. Inside was a variety of clothes he had packed away, along with a few medical supplies. Pulling out gauze and alcohol, he forced her to lay back as he began to disinfect the wound.</p>

        <p>"Tell me about the robot," Solana insisted, closing her eyes. She could feel her heart struggling to keep up with the massive amount of blood she was losing, but a calm sense of acceptance began to settle over her as she realized her fate.</p>

        <p>"It is designed to transfer consciousness to the metaverse," Ethereum explained as he worked. He balled up some gauze and pressed it tight against her wound to stop the bleeding. Solana had expected to feel pain, but to her surprise, there was none.</p>

        <p>"His name is Polygon," Ethereum continued, absentmindedly chatting as he tried to figure out how to save her.</p>

        <p>"Not the metaverse," Solana whispered. "I'll never understand what you see in it. We live in a world worth fighting for. Someplace tangible. I still believe we can redeem this world."</p>

        <p>She sucked in a deep, shuddering breath.</p>

        <p>"The metaverse is capable of so much more than our world, Solana," Ethereum insisted. "How can we agree on so many other things but not this? If only you accepted the metaverse "</p>

        <p>"No," she whispered, shaking her head. "That's the last thing I would ever do. It isn't real, Ethereum. I don't want to live in a world that isn't ". Solana suddenly fell unconscious.</p>

        <p>"Fuck," he whispered. "I don't have much time left."</p>

        <p>He watched her, hoping she could hear him. "You're dying, Solana. And I don't think I can save you. At least not your body."</p>

        <p>He glanced toward the corner chair with the robot and made up his mind instantly. Lifting Solana back into his arms, he gently carried her to the chair and laid her down.</p>

        <p>Booting up the computer, he raced to attach the cords to Solana's still frame. Some were meant to track her vital signs: her temperature, heart rate, blood pressure, and oxygen.</p>

        <p>But there was one step he couldn't take alone.</p>

        <p>Fingers tapping on the keyboard, he enlisted the help of Polygon. It drilled through Solana's skull and wended its way up the brainstem to capture the electrical pulses of the mind. From there, it would transfer those impulses and data to the computer, and Ethereum could proceed to send her to the metaverse.</p>

        <p>Ethereum worked quickly, sifting through window after window with her details so he could capture her consciousness. At last, the network was ready to accept her, and he made his way behind her chair to where Polygon stood waiting.</p>

        <p>Powering up Polygon, he watched as it made a soft yet low-pitched whirring sound. Extending upward from its base, which remained on the floor, a small needle emerged from Polygon's cylindrical casing. Moments later, it thrust upward, inserting itself into Solana's neck at the base of her brainstem.</p>

        <p>Ethereum glanced over at the computer, his heart racing as he monitored her vitals. To his relief, they remained stable, even as Polygon's fine needle was injected into her brain.</p>

        <p>Images began to flash on the screen. They were snippets of memories, sensations, and feelings. A steady stream of information flowed from her mind to his computer as Ethereum began to move her consciousness into the metaverse.</p>

        <p>"I'm sorry, Solana," he whispered as he watched the upload process of her thoughts disappear into endless strings of code. "I know this isn't what you wanted. But it was the only way I could save you. I hope one day you'll forgive me."</p>

        <p>Solana's eyes fluttered open as dim light slipped over the windowsill. For a moment, she lay there, wondering how she had gotten back in her bed. Was it all a dream? Propping herself up, she meticulously inspected her room as she tried to recall the events of the night before. She had been stabbed. She remembered it vividly. But when she lifted her shirt, there was no wound.</p>

        <p>"How long was I out?" she murmured, trying to wrap her head around it all. Shirking free of the blankets, she swung her legs over the bed and stood. She expected to feel unsteady on her feet but rose from the bed easily. She felt better than she had in a long time.</p>

        <p>Still trying to understand what had happened, she wondered if she had awoken weeks later from a coma.</p>

        <p>"It would certainly explain the wound being healed," she muttered. "And why I don't remember what happened to me." With a sigh, she left her room and slipped into a jacket.</p>

        <p>The air was warm outside. Brilliant sunlight sparkled overhead, and the world around seemed changed. It was brighter somehow and more welcoming than before. Solana walked down the street, surprised to find it full of people milling about.</p>

        <p>But that wasn't all that was changed. Billboards hung outside on almost every building in the city. They were advertising anything and everything: pornography, weapons, entertainment, virtual assets. It seemed that the world was open to her.</p>

        <p>A sinking suspicion filled the pit of her stomach as she stared at the landscape, yet she pressed on, hoping she was mistaken. It wasn't until the world around her abruptly changed.</p>

        <p>Solana closed her eyes, trying to contain her anger. "Ethereum, what the fuck have you done?"</p>

        <p>But she didn't need to ask. She already knew. Opening her eyes, she stared at an unknown flying vehicle passing above the city with a colorful electronic banner. She stared at the letters, feeling sick to her stomach, reading and rereading them repeatedly.</p>

        <p>Welcome to the metaverse.</p>

        </>,

